<template>
  <div class="tab_home">
    <van-nav-bar
      title="我的公告"
      left-text=""
      right-text=""
    />
    <van-list
      v-model="loading"
      :finished="finished"
      :immediate-check="false"
      @load="loadMore">
      <van-card
        v-for="(item, itemI) in items"
        class="order_list--van-card"
        :key="itemI"
      >
        <div slot="title" class="van-card__row">
          <div class="van-card__title">{{ item.title }}</div>
        </div>
        <div slot="desc" class="van-card__row">
          <div class="van-card__desc">{{item.content}}</div>
        </div>
        <div slot="desc" class="van-card__row">
          <div class="van-card__desc">{{item.create_time}}</div>
          <div class="van-card__num"></div>
        </div>
        <div slot="footer" class="order_list--footer_btn">
          <van-button size="small" type="danger" @click="toDetails(itemI)" >查看详情</van-button>
        </div>
      </van-card>
    </van-list>
    <is-empty v-if="isEmpty">暂无任务</is-empty>
  </div>
</template>

<script>
  import { NOTICE_LIST } from '@/api/user';
  import { Tab, Tabs, Panel, Card, Button, Toast, Popup, DatetimePicker, Row, Col, Field } from 'vant';
  import IsEmpty from "@/vue/components/is-empty/";
  import loadMore from '@/vue/mixin/list-load-more';

  export default {
    name: 'noticeList',
    mixins: [loadMore],
    data() {
      return {
        activeIndex : 2,
        currentDate: new Date(),
        showStart : false,
        showEnd : false,
        startDate: '',
        endDate:'',
        isEmpty: false
      }
    },

    created() {
      this.resetInit();
    },

    methods: {
      initData(loadMore = false) {
        return this.$reqGet(NOTICE_LIST, {
          per_page: this.pages.perPage,
          page: this.pages.currPage,
          tab_index: this.activeIndex
        }, {
          hideLoading: true
        }).then(res => {
          return this.loadSuccess(res);
        }).catch((err) => {

        })
      },
      loadSuccess(res){
        let list = res.data.data.list;
        if (!list) {
          setTimeout(() => {
            this.isEmpty = true
          }, 500)
          return this.pages;
        }
        this.isEmpty = false
        let page = {
          "pageCount":res.data.data.pageCount,
          "currPage":res.data.data.currPage,
          "perPage":res.data.data.perPage,
          "totalCount":res.data.data.totalCount
        };
        this.items.push(...list);
        return page;
      },
      onStartConfirm(value) {
        this.showStart = false;
        this.startDate = value;
      },
      onEndConfirm(value) {
        this.showEnd = false;
        this.endDate = value;
      },
      toDetails(i){
        let id = this.items[i].id;
        this.$router.push({ name: "noticeViews", params: { id: id} })
      },

      actionHandle(handle, i){
        this[handle] && this[handle](i);
      },
      addWork(){
        console.dir(111222)
        this.$router.push({ name: "workEdit" })
      }
    },
    components: {
      [Tab.name]: Tab,
      [Tabs.name]: Tabs,
      [Panel.name]: Panel,
      [Card.name]: Card,
      [IsEmpty.name]: IsEmpty,
      [Button.name]: Button,
      [Toast.name] : Toast,
      [Popup.name]: Popup,
      [DatetimePicker.name]: DatetimePicker,
      [Row.name]:Row,
      [Col.name]: Col,
      [Field.name]: Field
    }
  }

</script>

<style lang="scss" scoped>
  .van-search__action {
    padding: 0 10px;
    font-size: 14px;
    line-height: 30px;
    color: #666;
  }
  .order_list {
    background-color: #f9f9f9;
    padding-bottom: 0;
    &--van-card {
      padding:5px 15px;
    }
    &--footer_btn {
      text-align: right;
    }
    &--panel{
      margin-bottom: 10px;
    }
    &--van-card{
      background-color: #ffffff;
    }
    &--total{
      text-align: right;
      padding: 10px;
    }
  }
</style>



